import React, { useState } from 'react';

const LeadCaptureForm = ({ apiEndpoint }) => {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        company: '',
        phone: '',
        details: ''
    });
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [message, setMessage] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsSubmitting(true);
        setMessage('');

        try {
            const response = await fetch(apiEndpoint, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Origin': window.location.origin
                },
                body: JSON.stringify({
                    ...formData,
                    customFields: {
                        source: 'react-form',
                        timestamp: new Date().toISOString()
                    }
                }),
                mode: 'cors',
                credentials: 'omit'
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.error?.message || 'Submission failed');
            }

            const result = await response.json();
            setMessage('Thank you! We\'ll be in touch soon.');
            setFormData({ name: '', email: '', company: '', phone: '', details: '' });

        } catch (error) {
            setMessage('Something went wrong. Please try again.');
            console.error('Error:', error);
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };

    return (
        <div>
            <form onSubmit={handleSubmit} className="seva-form formkit-form">

                {/* Success/Error Message */}
                {message && (
                    <div className={`mb-4 p-4 rounded ${message.includes('Thank you')
                        ? 'bg-green-100 border border-green-400 text-green-700'
                        : 'bg-red-100 border border-red-400 text-red-700'
                        }`}>
                        {message}
                    </div>
                )}

                {/* Main form layout - matching existing style */}
                <div className='flex flex-col xl:flex-row space-x-8 justify-around my-1'>

                    {/* Name Input */}
                    <div className='basis-1/2 text-blue-700 font-semibold py-2 px-4 border border-yellow-500 shadow-md rounded my-1 xl:my-0'>
                        <input
                            type="text"
                            id="name"
                            name="name"
                            value={formData.name}
                            onChange={handleChange}
                            placeholder="Your Name *"
                            required
                            minLength={2}
                            maxLength={50}
                            className='bg-slate-100 w-full border-0 outline-0'
                        />
                    </div>

                    {/* Email Input */}
                    <div className='basis-1/2 text-blue-700 font-semibold py-2 px-4 border border-yellow-500 shadow-md rounded my-1 xl:my-0'>
                        <input
                            type="email"
                            id="email"
                            name="email"
                            value={formData.email}
                            onChange={handleChange}
                            placeholder="Your Email Address *"
                            required
                            className='bg-slate-100 w-full border-0 outline-0'
                        />
                    </div>

                    {/* Submit Button */}
                    <div>
                        <button
                            type="submit"
                            disabled={isSubmitting}
                            className='basis-1/2 bg-yellow-300 shadow-md hover:bg-yellow-500 text-blue-700 font-semibold hover:text-blue py-2 px-4 border border-yellow-500 hover:border-transparent rounded disabled:opacity-50 disabled:cursor-not-allowed'
                        >
                            {isSubmitting ? 'Submitting...' : 'Get Started'}
                        </button>
                    </div>

                </div>

                {/* Additional fields in a second row */}
                <div className='flex flex-col xl:flex-row space-x-8 justify-around my-4'>

                    {/* Company Input */}
                    <div className='basis-1/2 text-blue-700 font-semibold py-2 px-4 border border-yellow-500 shadow-md rounded my-1 xl:my-0'>
                        <input
                            type="text"
                            id="company"
                            name="company"
                            value={formData.company}
                            onChange={handleChange}
                            placeholder="Company (Optional)"
                            maxLength={100}
                            className='bg-slate-100 w-full border-0 outline-0'
                        />
                    </div>

                    {/* Phone Input */}
                    <div className='basis-1/2 text-blue-700 font-semibold py-2 px-4 border border-yellow-500 shadow-md rounded my-1 xl:my-0'>
                        <input
                            type="tel"
                            id="phone"
                            name="phone"
                            value={formData.phone}
                            onChange={handleChange}
                            placeholder="Phone (Optional)"
                            className='bg-slate-100 w-full border-0 outline-0'
                        />
                    </div>

                </div>

                {/* Details textarea - full width */}
                <div className='my-4'>
                    <div className='text-blue-700 font-semibold py-2 px-4 border border-yellow-500 shadow-md rounded'>
                        <textarea
                            id="details"
                            name="details"
                            value={formData.details}
                            onChange={handleChange}
                            placeholder="Additional details (Optional)"
                            rows={4}
                            maxLength={500}
                            className='bg-slate-100 w-full border-0 outline-0 resize-none'
                        />
                    </div>
                </div>

            </form>
        </div>
    );
};

export default LeadCaptureForm;